import Search from '@/components/header/header.vue'
import acquireString from '@/common/commonFunction.js'
export default {
	data() {
		return {
			verifyNumber:'',
			phoneNumber: '',
			code: '',
			countdown: '获取验证码',
			disabled: false,
			timestatus: false,
			timestatus2: true,
			clear: '',
			countown_style: {
				// 	width: '149upx',
				//zIndex: 2,
				// 	background: '#71D3BF',
				// 	border:''
				// 	// border: 1px solid rgba(168, 167, 167, 1);
			},
			//content: '获取验证码', // 按钮里显示的内容
			totalTime: 60 //记录具体倒计时时间 
		}
	},
	onLoad() {
		console.log(acquireString.randomWord(false, 4))
	},
	methods: {
		getCode() {
			this.countdown = 3;
			this.timestatus2 = false;
			this.timestatus = true;
			this.clear = setInterval(this.countDown, 1000);
			this.countown_style.zIndex = -1;
			uni.request({
				
				url: 'http://192.168.0.185:9999/code/phone/register?randomStr='+acquireString.randomWord(false, 4)+'&&phone='+13914612020,
				method: 'GET',
				success: res => {
					console.log(res);
					if (res.data.message == '验证码已发送') {
						// this.$router.push('/page/index/index/index');
						// console.log('登录成功');
						//  uni.navigateTo({
						// 	url: '../index/index/index',
						// }); 
						// uni.reLaunch({
						// 	url: '../index/index/index'
						// });
						uni.showToast({
							title: '发送成功',
							duration: 1000,
							icon: 'success',
						});
						console.log('获取验证码成功');
					} else {
						console.log('获取验证码失败');
					}
				}
			});
		},
		countDown() {
			if (!this.countdown) {
				this.disabled = false;
				this.timestatus = false;
				this.countdown = '获取验证码';

				clearInterval(this.clear);
				this.timestatus2 = true;
				this.countdown = '重新获取';
				this.countown_style.zIndex = 2;
			} else {

				--this.countdown;
			}
		}
		// 验证码倒计时 end 
	},
	components: {
		Search
	}
}
